import React from 'react';
import { Help } from '../../../ui/Help';
import { asView } from '../../../routing/View';
import { HelpSection } from '../../../ui/HelpSection';
import { Link } from '../../../navigation/Link';
import mark from './ColorChooser.svg';
import { Button } from '../../../ui/basic/Button';
import { useStateExtPersisted } from '../../../utility/store';
import { rand } from '../../../utility/random';
import { Section } from '../../../ui/Section';
import { SvgSymbol } from '../../../branding/SvgSymbol';
import { Mark } from '../../../ui/basic/Mark';
import styles from './ColorChooser.module.css';
import { Grid } from '../../../ui/Grid';
import { MarkRow } from '../../../ui/basic/MarkRow';

interface ColorMap<T> {
  r: T;
  g: T;
  b: T;
}
interface Color extends ColorMap<number> { }
interface ColorAdjust extends Partial<Color> { }
type ColorId = keyof Color;
type ColorMapFn<T> = (value: number, c: ColorId) => T;

const mapColors = <T extends any>(color: Color, map: ColorMapFn<T>): ColorMap<T> => {
  return {
    r: map(color.r, 'r'),
    g: map(color.g, 'g'),
    b: map(color.b, 'b'),
  };
};

const mapColorValues = <T extends any>(color: Color, map: ColorMapFn<T>) => {
  return Object.values(mapColors(color, map));
};

const colorToHex = (color: Color): string => {
  const mapped = mapColors(color, v => ('0' + v.toString(16)).substr(-2));
  return `#${mapped.r}${mapped.g}${mapped.b}`;
};

const ifColor = (value: number, original: number): number => {
  if (0 <= value && 0xFF >= value) {
    return value;
  }
  return original;
};

const mapColorsSafe = (color: Color, map: ColorMapFn<number>): Color => {
  return mapColors(color, (v, i) => {
    return ifColor(map(v, i), v);
  });
};

const chooseColor = (): Color => {
  const choice = mapColorsSafe({
    r: rand(0, 255),
    g: rand(0, 255),
    b: rand(0, 255),
  }, v => Math.round((v) / 15) * 15);
  return choice;
};

const adjustColor = (color: Color, adjustment: ColorAdjust): Color => {
  return mapColorsSafe(color, (v, i) => {
    return v + (adjustment[i] ?? 0);
  });
};

const complement = (color: Color): Color => {
  return mapColorsSafe(color, v => 0xFF - v);
};

const muted = (color: Color): Color => {
  const average = mapColorValues(color, v => v).reduce((r, v) => r + v, 0) / 3;
  return mapColorsSafe(color, v => Math.round((average + v) / 2));
};

const vibrant = (color: Color): Color => {
  const mutedColor = muted(color);
  return mapColorsSafe(color, (v, i) => {
    return v + (v - mutedColor[i]);
  });
};

const brighter = (color: Color): Color => {
  return mapColorsSafe(color, v => Math.round((v + 0xFF) / 2));
};

const darker = (color: Color): Color => {
  return mapColorsSafe(color, v => Math.round(v / 2));
};

export const ColorChooserMark = () => {
  return <SvgSymbol src={mark} alt='color chooser mark' />;
};

interface ColorBlockProps {
  color: Color;
  label?: string;
}

const ColorBlock: React.FC<ColorBlockProps> = (props) => {
  const { color, label } = props;
  const prefix = label ? label + ' ' : '';
  const colorHex = colorToHex(color);
  return (
    <Mark name={prefix + colorHex}>
      <div className={styles.colorBlock} style={{ backgroundColor: colorHex }}></div>
    </Mark>
  );
};

export interface ColorChooserProps {
}

export const ColorChooser: React.FC<ColorChooserProps> = (props) => {
  const [color, setColor] = useStateExtPersisted('color', chooseColor())
  const compColor = complement(color);

  return (
    <>
      <Help>
        <HelpSection label='What is this?'>
          Color Chooser is meant to help decide on a color. Although it is
          setup to help with websites (since it provides
          the <Link to='https://en.wikipedia.org/wiki/Web_colors' external>web color information</Link>)
          it can also be used to choose a color for almost any purpose.
        </HelpSection>

        <HelpSection label='How do I share colors?'>
          All adjustments to the color is saved in the URL. To share a color,
          just copy and paste the URL.
        </HelpSection>
      </Help>

      <Section>
        <Grid values={[
          <Button action={() => setColor(chooseColor)} wide>Choose Color</Button>,
          <>
            <Grid values={[
              <Button action={() => setColor(adjustColor(color, { r: 15 }))}>+</Button>,
              <span className={styles.valueR}>R: {color.r}</span>,
              <Button action={() => setColor(adjustColor(color, { r: -15 }))}>-</Button>
            ]} />
            <Grid values={[
              <Button action={() => setColor(adjustColor(color, { g: 15 }))}>+</Button>,
              <span className={styles.valueG}>G: {color.g}</span>,
              <Button action={() => setColor(adjustColor(color, { g: -15 }))}>-</Button>
            ]} />
            <Grid values={[
              <Button action={() => setColor(adjustColor(color, { b: 15 }))}>+</Button>,
              <span className={styles.valueB}>B: {color.b}</span>,
              <Button action={() => setColor(adjustColor(color, { b: -15 }))}>-</Button>
            ]} />
          </>
        ]} />
      </Section>

      <Section border>
        <MarkRow>
          <ColorBlock color={color} />
        </MarkRow>
      </Section>

      <Section label='Adjusted' border>
        <MarkRow>
          <ColorBlock label='Muted' color={muted(color)} />
          <ColorBlock label='Vibrant' color={vibrant(color)} />
          <ColorBlock label='Brighter' color={brighter(color)} />
          <ColorBlock label='Darker' color={darker(color)} />
        </MarkRow>
      </Section>

      <Section label='Complement' border>
        <MarkRow>
          <ColorBlock label='Muted' color={muted(compColor)} />
          <ColorBlock label='Vibrant' color={vibrant(compColor)} />
          <ColorBlock label='Brighter' color={brighter(compColor)} />
          <ColorBlock label='Darker' color={darker(compColor)} />
        </MarkRow>
      </Section>
    </>
  );
};

export const ColorChooserView = asView(ColorChooser, {
  header: 'Color Chooser',
});